        , acuFinanceStkInfo: []
        , acuInsuranceStkInfo: []
        , acuInvestmentStkInfo: []
        , acuBankStkInfo: []
        , acuEtcFinanceStkInfo: []
        , acuPensionFundStkInfo: []
        , acuGovernmentStkInfo: []
        , acuEtcCorpStkInfo: []
        , acuEtcForeignerStkInfo: []
        , acuPrivateEquityStkInfo: []
        , acuGrossSumStkInfo: []
        
        // 1  3  
        , indiDispersionArr: []
