<template>
  <v-app>
    <div class="loader" v-if="loading">  .  .....</div>
    <v-radio-group v-model="row" row column :disabled=loading>
        <v-radio label="1" value="radio-1" @click=interest1 @change=interest1Change />
        <v-radio label="2" value="radio-2" @click=interest2></v-radio>
      </v-radio-group>
    <div v-if="inter1">
      <table width="100%">
        <tr><td>FROM():</td>
          <td>
            <vue-englishdatepicker classValue="datepicker" placeholder="YYYY-MM-DD"
              format="YYYY-MM-DD" @change="changeFromDate" :value="fromdate"/>
          </td>
          <td>TO():</td>
          <td>
            <vue-englishdatepicker classValue="datepicker" placeholder="YYYY-MM-DD"
              format="YYYY-MM-DD" @change="changeToDate" :value="todate" />
          </td>
        </tr>
        </table>
        <table>
          <tr>
            <td><v-checkbox v-model="individual" :label="``" /></td>
            <td><v-checkbox v-model="grossSum" @click="changeGrossSum" :label="``"/></td> 
            <td><v-checkbox v-model="foreigner" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="finance" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="insurance" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="investment" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="bank" @click="changeNotGrossSum(e)" :label="``"/></td>
          </tr>
          <tr>
            <td><v-checkbox v-model="etcFinance" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="pensionFund" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="government" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="etcCorp" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="etcForeigner" @click="changeNotGrossSum(e)" :label="``"/></td>
            <td><v-checkbox v-model="privateEquity" @click="changeNotGrossSum(e)" :label="``"/></td>
          </tr>
        </table>
        <v-simple-table>
        <thead>
          <tr>
            <th class="text-left">
              (){{ inOnLftRowLength }}
            </th>
            <th class="text-left">
              ()
            </th>
             <th class="text-left">
               <v-btn
                color="primary"
                elevation="4"
                small
                v-on:click="onBtnExport()"
              >CSV </v-btn>
            </th>
          </tr>
        </thead>
        <!-- <tbody><tr><td></td><td></td></tr></tbody> -->
    </v-simple-table>
    </div>
    <ag-grid-vue 
        style="width: 100%; height: 100%;"
        class="ag-theme-alpine"
        :columnDefs="columnDefs"
        :rowData="this.$store.state.rowData"
        rowSelection="single"
        @grid-ready="onGridReady"
        @selection-changed="getSelectedRows"
        :defaultColDef="this.$store.state.defaultColDef">
    </ag-grid-vue>
  </v-app>
</template>
<script>
import { AgGridVue } from "ag-grid-vue";
import axios from "axios";
import VueEnglishdatepicker from 'vue-englishdatepicker';
import {mapGetters, mapState, mapMutations, mapActions} from 'vuex';
import {CommonUtil} from '../CommonUtil';

export default {
  computed: {
    ...mapGetters(['inOnLftRowLength', 'getStkNm']),
    /**  
     * ...mapGetters({
     *    lth: 'inOnLftRowLength', //   , 'inOnLftRowLength' lth .
     *    stockName: 'getStkNm', //   , 'getStkNm' stockName .
     * })
     */
    ...mapState(['inOnLftRowData', 'rowData'])
  },
  created() {
     console.log('created');
  },
  beforeMount() {
    console.log('beforeMount');
    const commonUtil = new CommonUtil(); 
    this.columnDefs = [
      { headerName: '', field: 'stockName', sortable: true, filter: true, width: 120,},
      { headerName: '', field: 'individual', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'grossSum', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'foreigner', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'finance', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'insurance', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'investment', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'bank', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'etcFinance', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'pensionFund', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '()', field: 'government', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'etcForeigner', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'privateEquity', sortable: true, filter: true, valueFormatter: commonUtil.curruncyFormatter, cellStyle: commonUtil.cellStyleFormatter, width: 90,},
      { headerName: '', field: 'fileTitle', sortable: true, filter: true, hide: true }
    ];
    let date = new Date();
    let year = date.getFullYear();
    let month = ("0" + (1 + date.getMonth())).slice(-2);
    let day = ("0" + date.getDate()).slice(-2);
    this.todate = year + '-' +month + '-' + day;
    // this.fromdate = year + '-' +month + '-' + day;
    this.fromdate =  '2020-12-01';
    this.$store.state.inOnLftRowData = [];
    // this.$store.state.rowData = [];
    // this.emptyRowData([]);
    // this.$store.commit('emptyRowData', []); // commit (  .)
    this.$store.dispatch('emptyRowData', []); // dispatch (   .)
  },
  mounted(){
    //   
    /**  
     * //  
     * EventBus.$on('signUp', users => {
     *   this.allUsers.push(users);
     * });
     */

    /**
     * //  
     * EventBus.$emit('signUp', userObj);
     */
  },
  name: 'QuickInterestStock',
  methods: {
     onBtnExport() {
      this.gridApi.exportDataAsCsv(); 
     },
    ...mapMutations([]), // 'emptyRowData'
    ...mapActions(['emptyRowData']),
    reset(){
      this.success = false;
      this.error = false;
    },
    keyFromDate(e){
      console.log('keyUpFromDate e;;;'+e);
    },
    changeNotGrossSum(e){
      console.log('changeNotGrossSum e;;;'+e);
      if(this.finance && this.foreigner && this.finance && this.insurance
      && this.investment && this.bank && this.etcFinance && this.pensionFund
      && this.government && this.etcCorp && this.etcForeigner && this.privateEquity){
        this.grossSum = true;
        return;
      }
      if(!e){
        this.grossSum = false;
        return;
      }
    },
    changeGrossSum(){
      console.log('  ...'); 
      this.foreigner = this.grossSum;
      this.finance = this.grossSum;
      this.insurance = this.grossSum;
      this.investment = this.grossSum;
      this.bank = this.grossSum;
      this.etcFinance = this.grossSum;
      this.pensionFund = this.grossSum;
      this.government = this.grossSum;
      this.etcCorp = this.grossSum;
      this.etcForeigner = this.grossSum;
      this.privateEquity = this.grossSum;
    },
    changeFromDate(e){
      this.fromdate = e;
    },
    changeToDate(e){
      this.todate = e;
    },

    async interest1() {
      this.inter1 = true;
      if(this.fromdate === undefined){
        let date = new Date();
        let year = date.getFullYear();
        let month = ("0" + (1 + date.getMonth())).slice(-2);
        let day = ("0" + date.getDate()).slice(-2);
        this.fromdate = year + '-'+month + '-'+day;
        // console.log('interest1 undefined this.fromdate', this.fromdate);
      }
      if(this.todate === undefined){
        let date = new Date();
        let year = date.getFullYear();
        let month = ("0" + (1 + date.getMonth())).slice(-2);
        let day = ("0" + date.getDate()).slice(-2);
        this.todate = year + '-'+ month +'-'+ day;
        // console.log('interest1 undefined this.todate', this.todate);
      } 
      if(parseInt(this.fromdate.replace(/-/gi, ""))>parseInt(this.todate.replace(/-/gi, ""))){
        alert('toDate fromDate   . \n !');
        return;
      }
      let ths = this;
      let postData = {
        fromdate: this.fromdate, 
        todate: this.todate,
        checkbx: {
          individual: this.individual,
          grossSum: this.grossSum,
          foreigner: this.foreigner,
          finance: this.finance,
          insurance: this.insurance,
          investment: this.investment,
          bank: this.bank,
          etcFinance: this.etcFinance,
          pensionFund: this.pensionFund,
          government: this.government,
          etcCorp: this.etcCorp,
          etcForeigner: this.etcForeigner,
          privateEquity: this.privateEquity,
        },
      };
      this.loading = true;
      this.$store.state.inOnLftRowData = [];
      // this.$store.state.rowData = []; // mapState  , this.rowData;
      // this.emptyRowData([]);
      // this.$store.commit('emptyRowData', []); // mapMutations  , this.emptyRowData([]);
      //   , dispatch  .
      this.$store.dispatch('emptyRowData', []); // mapActions  , this.emptyRowData([]);
      axios.post('http://supplydemand.iptime.org/supplydemand/api/leftFastList/',
        {
          headers: {
              'Content-Type': 'application/json',
              'Authorization': 'JWT fefege...'
        }, postData})
        .then(function(response) {
          
          ths.reset();
          ths.success = true;
          console.log(response);
          ths.$store.state.inOnLftRowData = response.data;
          ths.$store.state.rowData = response.data;
        })
        .catch(function(error) {
          ths.error = true;
          console.log(error);
        })
        .finally(()=>{
          ths.loading = false;
        });
    },

    interest1Change(){
      console.log('inter1 changed...');

    },

    async interest2() {
      this.inter1 = false;
      this.inter2 = true;
      console.log("interest2()...");
    },

    onGridReady(params) {
      this.gridApi = params.api;
      this.columnApi = params.columnApi;
      //this.gridApi.sizeColumnsToFit();
    },

    getSelectedRows() {
      const selectedNodes = this.gridApi.getSelectedNodes();
      const selectedData = selectedNodes.map(node => node.data);
      const csvFileName = selectedData[0].fileTitle;
      const stockName = selectedData[0].stockName;
      console.log('selectedData1 fileTitle: ' + csvFileName);
      console.log('selectedData1 selectedData[0]: ' + selectedData[0]);
      console.log('selectedData2: ' + this.gridApi.getSelectedRows());
      let resData = {};
      let ths = this;
      let postData = {
        csvFileName,
        fromdate: this.fromdate, 
        todate: this.todate,
      };
      axios.post('http://supplydemand.iptime.org/supplydemand/api/rightChartList/',
      {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': 'JWT fefege...'
      }, postData})
      .then(function(response) {
        console.log(response);
        resData = {
          resultStockInfo: response.data.resultStockInfo,
          stockName,
        };
        const commonUtil = new CommonUtil(); 
        ths.$emit('showchart', resData);
        // (close price)
        ths.$store.state.inOnLftClkStkNm = stockName;
        // 
        ths.$store.state.acuIndividualStkInfo = commonUtil.changeDate(response.data.acuIndividualStkInfo);
        ths.$store.state.acuForeignerStkInfo = commonUtil.changeDate(response.data.acuForeignerStkInfo);
        ths.$store.state.acuFinanceStkInfo = commonUtil.changeDate(response.data.acuFinanceStkInfo);
        ths.$store.state.acuInsuranceStkInfo = commonUtil.changeDate(response.data.acuInsuranceStkInfo);
        ths.$store.state.acuInvestmentStkInfo = commonUtil.changeDate(response.data.acuInvestmentStkInfo);
        ths.$store.state.acuBankStkInfo = commonUtil.changeDate(response.data.acuBankStkInfo);
        ths.$store.state.acuEtcFinanceStkInfo = commonUtil.changeDate(response.data.acuEtcFinanceStkInfo);
        ths.$store.state.acuPensionFundStkInfo = commonUtil.changeDate(response.data.acuPensionFundStkInfo);
        ths.$store.state.acuGovernmentStkInfo = commonUtil.changeDate(response.data.acuGovernmentStkInfo);
        ths.$store.state.acuEtcCorpStkInfo = commonUtil.changeDate(response.data.acuEtcCorpStkInfo);
        ths.$store.state.acuEtcForeignerStkInfo = commonUtil.changeDate(response.data.acuEtcForeignerStkInfo);
        ths.$store.state.acuPrivateEquityStkInfo = commonUtil.changeDate(response.data.acuPrivateEquityStkInfo);
        ths.$store.state.acuGrossSumStkInfo = commonUtil.changeDate(response.data.acuGrossSumStkInfo);
        // 
        ths.$store.state.indiDispersionArr = commonUtil.changeDate(response.data.indiDispersionArr);
        ths.$store.state.foreignerDispersionArr = commonUtil.changeDate(response.data.foreignerDispersionArr);
        ths.$store.state.financeInvestDispersionArr = commonUtil.changeDate(response.data.financeInvestDispersionArr);
        ths.$store.state.insuranceDispersionArr = commonUtil.changeDate(response.data.insuranceDispersionArr);
        ths.$store.state.assetManageDispersionArr = commonUtil.changeDate(response.data.assetManageDispersionArr);
        ths.$store.state.bankDispersionArr = commonUtil.changeDate(response.data.bankDispersionArr);
        ths.$store.state.etcFinanceDispersionArr = commonUtil.changeDate(response.data.etcFinanceDispersionArr);
        ths.$store.state.pensionFundDispersionArr = commonUtil.changeDate(response.data.pensionFundDispersionArr);
        ths.$store.state.governmentDispersionArr = commonUtil.changeDate(response.data.governmentDispersionArr);
        ths.$store.state.etcCoporDispersionArr = commonUtil.changeDate(response.data.etcCoporDispersionArr);
        ths.$store.state.etcForeignerDispersionArr = commonUtil.changeDate(response.data.etcForeignerDispersionArr);
        ths.$store.state.privateEquityDispersionArr = commonUtil.changeDate(response.data.privateEquityDispersionArr);
        ths.$store.state.grossSumDispersionArr = commonUtil.changeDate(response.data.grossSumDispersionArr);
        ths.$store.state.resultRowData = response.data.resultRowData;
        ths.$store.state.averagePriceRowData = response.data.averagePriceRowData;

      })
      .catch(function(error) {
        console.log(error);
      })
      .finally({});
    },
  },
  components: {
    AgGridVue,
    VueEnglishdatepicker,
  },
  data: () => ({
    // spinner
    success: false,
    error: false,
    loading: false,

    // ag grid 
    columnDefs: null,
    
    // 1  
    inter1: false,
    // 1 : 
    individual: false,
    // 1 : 
    grossSum: true,
    // 1 : 
    foreigner: true,
    // 1 : 
    finance: true,
    // 1 : 
    insurance: true,
    // 1 : 
    investment: true,
    // 1 : 
    bank: true,
    // 1 : 
    etcFinance: true,
    // 1 : 
    pensionFund: true,
    // 1 : 
    government: true,
    // 1 : 
    etcCorp: true,
    // 1 : 
    etcForeigner: true,
    // 1 : 
    privateEquity: true,
    // ///////////////////////////////////////////////////
    // 2  
    inter2: false,
    
    
  }),
}
</script>
